.task-form-layout {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: calc(100vh - 120px);
}
.task-form-content {
  flex: 1;
  min-height: 0;
  overflow-y: auto;
  overflow-x: hidden;
  padding-right: 4px;
  overscroll-behavior: contain;
}
.task-form-footer {
  flex-shrink: 0;
  background: inherit;
  border-top: 1px solid var(--border);
  padding-top: 1rem;
  margin-top: auto;
}
.input-focus-style {
  transition: border-color 0.2s, box-shadow 0.2s;
}
.input-focus-style:focus {
  outline: none;
  border-color: var(--color-main-400);
  box-shadow: 0 0 0 3px rgba(106, 146, 154, 0.15);
}
.dark .input-focus-style:focus {
  border-color: var(--color-main-300);
  box-shadow: 0 0 0 3px rgba(106, 146, 154, 0.25);
}
.calendar-portal {
  position: fixed;
  z-index: 9999;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  padding: 8px;
}
.calendar-portal .react-calendar-custom {
  animation: calendar-fade-in 0.15s ease-out;
}
@keyframes calendar-fade-in {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
